import React from "react";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC, AshowDemoHOC } = HOC;
import doc from "zerod/components/Zlayout/doc.md";
import { Zlayout } from "zerod";
export default AmdDocHOC(doc, {
	demo1: function() {
		const MyScript = AshowDemoHOC(
			<Zlayout flexRow>
				<Zlayout width={200} style={{ background: "#CBC9C9" }} />
				<Zlayout style={{ background: "#EAE8E8" }} />
				<Zlayout width={200} style={{ background: "#D1D1D1" }} />
			</Zlayout>,
			false,
		);
		return <MyScript />;
	},
	demo2: function() {
		const MyScript = AshowDemoHOC(
			<Zlayout flexRow>
				<Zlayout width={200}>
					<Zlayout.Zheader height="64" style={{ background: "#EAEAEA" }}>上</Zlayout.Zheader>
					<Zlayout.Zbody style={{ background: "#CBCBCB" }}>中</Zlayout.Zbody>
					<Zlayout.Zfooter height="50" style={{ background: "#C6C6C6" }}>下</Zlayout.Zfooter>
				</Zlayout>
				<Zlayout>
					<Zlayout.Zheader height="64" style={{ background: "#E8E0E0" }}>上</Zlayout.Zheader>
					<Zlayout.Zbody style={{ background: "#DBD1D1" }}>中</Zlayout.Zbody>
					<Zlayout.Zfooter height="50" style={{ background: "#CEBFBF" }}>下</Zlayout.Zfooter>
				</Zlayout>
			</Zlayout>,
			false,
		);
		return <MyScript />;
	},
});
